.goods{
    position: absolute;
    top: 1.74rem;
    bottom: 0;
    left: 0;
    right: 0;
    background: rgb(201, 75, 75);
    overflow: hidden;
}
.goods>.scroll{
    width: 100%;
    height: 100%;
}
.goods>.scroll .item{
    height: 1rem;
    background: yellowgreen;
}


